

main {
  margin: 0 30px;
}

.first-benefit, .second-benefit, .third-benefit {
  background-color: $color-white;
  margin: 15px auto;
}

.left-side-first, .right-side-second, .left-side-third {
  h2 {
    font-family: $raleway-font;
    font-size: 30px;
    margin-bottom: 20px;
  }
  p{
    font-family: $open-sans-font;
    font-size: 14px;
  }
}

@media (max-width: $mobile) {
  .first-benefit, .second-benefit, .third-benefit {
    height: 370px;
  }
  .first-benefit {
    display: flex;
    flex-direction: column;
    .left-side-first {
      order: 2;
      h2 {
        margin-top: 60px;
      }
      p {
        line-height: 20px;
        margin-top: 60px;
      }
    }

    .right-side-first {
      order: 1;
      .first-benefit-square{
        margin-left: 130px;
        width: 100px;
        height: 100px;
        background-color: $color-redish;
        position: relative;
        border-radius: 40px;
        transform: rotate(135deg);
        .right-side-first-image {
          width: 200px;
          height: 100px;
          transform: rotate(225deg);
          margin-left: 20px;
          margin-top: 30px;
        }
      }
    }
  }
  .second-benefit {
    display: flex;
    flex-direction: column;
    .left-side-second-image {
      width: 170px;
      height: 140px;
      margin-top: 10px;
    }
    .second-benefit-circle {
      margin-left: 90px;
      width: 130px;
      height: 130px;
      position: relative;
      border-radius: 50%;
      background-color: $color-primary;
    }
    .right-side-second {
      h2 {
        margin-top: 40px;
      }
      p {
        line-height: 20px;
        margin-top: 30px;
      }
    }
  }
  .third-benefit {
    display: flex;
    flex-direction: column;
    padding: 0 !important;
    .left-side-third {
      margin: 0 15px;
      h2 {
        margin-top: 30px;
      }
      p {
        line-height: 20px;
        margin-top: 30px;
      }
    }
    .right-side-third {
      .right-side-third-image {
        margin-top: 20px;
        height: 80%;
        width: 100%;
        border-radius: 5px;
        background-color: $color-green;
        .right-side-third-picture {
          margin-top: 30px;
        }
      }
    }
  }
  main {
    margin-bottom: 120px;
  }
}

.right-side-first-picture, .left-side-second-picture, .right-side-third-picture {
  width: 100%;
  height: 100%;
}



@media (min-width: $mobile) {
  main {
    .container {
      .row {
        @include center;
        flex-direction: column;
        .first-benefit, .second-benefit, .third-benefit {
          height: 350px;
          @include center;
          justify-content: space-between;
          flex-direction: row;
          .left-side-first {
            width: 50%;
            position: relative;
            p {
              width: 85%;
              line-height: 20px;
            }
            p:after {
              content: "";
              width: 70%;
              height: 5px;
              background-color: $color-redish;
              position: absolute;
              bottom: -30px;
              left: 0;
            }
          }
          .right-side-first {
            margin-right: 5%;
            max-width: 300px;
            width: 50%;
            .right-side-first-image {
              position: relative;
              z-index: 0; //  >= 0 wtedy before jest widoczny
            }
            .right-side-first-image:before {
              position: absolute;
              content: "";
              width: 40%;
              height: 70%;
              background-color: $color-redish;
              top: -15%;
              left: 70%;
              z-index: -1;  //chowa za
              border-radius: 30px;
              transform: rotate(135deg);
            }
          }
          .left-side-second {
            width: 40%;
            margin-left: 5%;
            .left-side-second-image {
              position: relative;
              z-index: 0;
            }
            .left-side-second-image:before {
              content: "";
              width: 100%;
              height: 100%;
              position: absolute;
              border-radius: 50%;
              background-color: $color-primary;
              top: -10px;
              left: -10px;
              z-index: -1;
            }
          }
          .right-side-second {
            width: 40%;
            p {
              position: relative;
              line-height: 20px;
            }
            p:after {
              content: "";
              width: 85%;
              height: 5px;
              background-color: $color-primary;
              position: absolute;
              bottom: -30px;
              left: 0;
            }
          }
        }
        .third-benefit {
          padding: 0;
          background-color: transparent;
          .left-side-third {
            padding: 15px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            width: 60%;
            height: 100%;
            background-color: $color-white;
            h2 {
              width: 60%;
            }
            p {
              position: relative;
              line-height: 20px;
              width: 90%;
            }
            p:after {
              content: "";
              width: 62%;
              height: 5px;
              background-color: $color-green;
              position: absolute;
              bottom: -30px;
              left: 0;
            }
          }
          .right-side-third {
            width: 35%;
            height: 100%;
            background-color: $color-green;
            .right-side-third-image {
              padding: 50px 30px 30px 30px;
              width: 100%;
              height: 70%;
            }
          }
        }
      }
    }
  }
}


// upper end



@media (max-width: $mobile) {
  .pricing-offers {
    display: flex;
    flex-direction: column;
  }
  .basic, .premium, .professional {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    position: relative;
    .offer-options {
      ul {
        margin: 20px 0;
        li {
          margin-bottom: 20px;
        }
      }
    }
  }
  .professional {
  .offer-options {
    ul {
      margin: 40px 0;
    }
  }
  }
  .basic, .premium {
    margin: 30px;
  }
  .professional {
    margin: 30px 10px 0;
  }
  .basic-left, .professional-left, .premium-left {
    text-align: center;
  }
  .basic-left {
    width: 25%;
  }
}

//mobile end


.pricing-title {
  font-family: $raleway-font;
  font-size: 36px;
}


.basic, .premium, .professional {
  border: 1px solid $color-redish;
  border-radius: 10px;
    h3 {
    font-family: $raleway-font;
    font-size: 20px;
    margin-bottom: 10px;
    font-weight: bold;
   }
   span {
    font-family: $open-sans-font;
    font-size: 40px;
   }
}
.professional {
  border-color: $color-primary;
}
.premium {
  border-color: $color-green;
}
.professional, .premium {
  .limited {
    font-family: $raleway-font;
    font-size: 12px;
    font-weight: bold;
    margin-top: 10px;
  }
}
.basic:after, .premium:after, .professional:after{
  content: "Begin";
  width: 150px;
  height: 50px;
  border-radius: 30px;
  position: absolute;
  background-color: $color-redish;
  @include center;
  color: $color-white;
  font-family: $open-sans-font;
  font-size: 13px;
  font-weight: bold;
  cursor: pointer;
  top: 205px;
}
.professional:after {
  top: 245px;
}

@media (min-width: $mobile) {
  .pricing {
    margin: 100px 0;
    margin-bottom: 0;
    .pricing-offers {
      display: flex;
      flex-direction: column;
      align-items: center;
      .offers-row {
        width: 100%;
        height: 500px;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
      }
      .pricing-title {
        margin-top: 80px;
      }
    }
  }
  .basic, .professional, .premium {
    position: relative;
    max-width: 31% !important;
    @include center;
    flex-direction: column;
    text-align: center;
    .offer-options {
      text-align: center;
      margin-top: 60px;
      ul {
        li {
          font-family: $open-sans-font;
          margin-bottom: 20px;
        }
        li:last-of-type {
          padding-bottom: 20px;
        }
      }
    }
    .basic-left {
      padding-bottom: 20px;
    }
  }
  .professional {
    height: 420px;
  }
  .basic:after, .professional:after, .premium:after {
    position: absolute;
    top: 355px;
    left: 25%;
  }
  .basic:after {
    background-color: $color-redish;
  }
  .professional:after {
    top: 395px;
    background-color: $color-primary;
  }
  .premium:after {
    background-color: $color-green;
  }
}





.pricing {
  background-color: $color-white;
}

.pricing-offers {
  .row {
    .basic {
      span {
       color: $color-redish;
      }
    }
    .professional {
      span {
        color: $color-primary;
      }
    }
    .premium {
      span {
        color: $color-green;
      }
    }
    .basic, .professional, .premium {
      .offer-options {
        ul {
          li {
            position: relative;
          }
          li:before {
            content: "";
            width: 11px;
            height: 11px;
            display: inline-block;
            margin-right: 10px;
            background-image: url("/assets/Checkmark-1.svg");
          }
          .not:before {
            content: "";
            width: 11px;
            height: 11px;
            display: inline-block;
            margin-right: 10px;
            background-image: url("/assets/Close.svg");
          }
        }
      }
    }
  }
}


@media (min-width: $mobile) and (max-width: $tablet) {
  .pricing {
    margin-bottom: 800px;
    background-color: transparent;
  }
  .basic, .professional, .premium {
    max-width: 100% !important;
    background-color: $color-white;
  }
  .basic:after, .professional:after, .premium:after {
    position: absolute;
    top: 355px;
    left: 40%;
  }
  .professional:after {
    top: 395px;
  }
}





